@font-face{font-family:'Squada One';font-style:normal;font-weight:400;src:local("Squada One"),local("SquadaOne-Regular"),url(https://fonts.gstatic.com/s/squadaone/v8/BCasqZ8XsOrx4mcOk6Mtaac2WRJnDgo.woff2) format("woff2");unicode-range:U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD}html,body{margin:0;position:relative;color:#ffffff;font-family:'Squada One';overflow-x:hidden;overflow-y:hidden}html div.links,body div.links{position:absolute;top:20px;left:40px}html div.links a,body div.links a{text-decoration:none}html div.links a i,body div.links a i{font-size:40px;color:#000000;margin-right:20px}html div.links a i:hover,body div.links a i:hover{cursor:pointer}html div.container,body div.container{background-color:#2b6aea;height:38px;min-width:270px;position:absolute;top:0;right:-20px;transform:skewX(45deg)}html div.container .time-word,body div.container .time-word{color:white;display:inline-block;font-size:36px;margin-right:200px;right:-30px;position:relative;padding-right:50px;transform:skewX(-45deg)}html div.container div.inner-container,body div.container div.inner-container{background-color:#2b6aea;display:flex;justify-content:flex-end;height:54px;min-width:180px;position:absolute;right:10px;transform:none}html div.container div.inner-container .stopwatch,body div.container div.inner-container .stopwatch{background-color:#2b6aea;display:inline-block;font-size:50px;padding-right:40px;margin-left:20px;transform:skewX(-45deg)}html div.game-over,body div.game-over{background-color:#2b6aea;display:flex;flex-direction:column;align-items:center;font-size:30px;height:100px;position:absolute;top:200px;left:200px;width:220px}html div.game-over span,body div.game-over span{display:block;padding-top:10px}html div.game-over.hidden,body div.game-over.hidden{display:none}
